import React, { Component } from "react";
import * as S from "./styled";

const PortfolioListContent = [
  {
    image: "./assets/images/preview/happyx-project-image.jpg",
    category: "In Development",
    title: "Happy X",
  },
  {
    image: "./assets/images/preview/sok-project-image.jpg",
    category: "In Development",
    title: "Swedish Olympic Committee",
  },
  {
    image: "./assets/images/preview/atm-project-image.jpg",
    category: "Alpha",
    title: "ATM",
  },
  {
    image: "happyx-preview",
    category: "Development",
    title: "Getting tickets to the big show",
  },
  {
    image: "happyx-preview",
    category: "Development",
    title: "Getting tickets to the big show",
  },
  {
    image: "happyx-preview",
    category: "Development",
    title: "Getting tickets to the big show",
  },
];

class PortfolioList extends Component {
  render() {
    const { column, styevariation } = this.props;
    const list = PortfolioListContent.slice(0, this.props.item);
    return (
      <React.Fragment>
        {list.map((value, index) => (
          <div className={`${column}`} key={index}>
            <div className={`portfolio ${styevariation}`}>
              <S.div>
                <S.img src={value.image} alt="HappyX Project"></S.img>
                <div className="content">
                  <div className="inner">
                    <p
                      className="category"
                      style={{ opacity: 0, fontSize: 30, color: "#636060 " }}
                    >
                      {value.category}
                    </p>
                    <h4>
                      <a
                        className="title"
                        style={{ opacity: 0, fontSize: 30, color: "#111111 " }}
                        href="/portfolio-details"
                      >
                        {value.title}
                      </a>
                    </h4>
                    <div style={{ opacity: 0 }} className="portfolio-button">
                      <a
                        className="rn-button-style--2 btn-solid"
                        href="/happyx-project"
                      >
                        <span>View Project</span>
                      </a>
                    </div>
                  </div>
                </div>
              </S.div>
            </div>
          </div>
        ))}
      </React.Fragment>
    );
  }
}
export default PortfolioList;
