import React, { Component } from "react";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import { Link, animateScroll as scroll } from "react-scroll";
import { Line, Circle } from "rc-progress";

class TabsTwo extends Component {
  render() {
    let tab1 = "Skillset",
      tab2 = "Experience",
      tab3 = "Education";
    const { tabStyle } = this.props;
    return (
      <div>
        {/* Start Tabs Area */}
        <div className="tabs-area">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <Tabs>
                  <TabList className={`${tabStyle}`}>
                    <Tab>{tab1}</Tab>
                    <Tab>{tab2}</Tab>
                    <Tab>{tab3}</Tab>
                  </TabList>

                  <TabPanel>
                    <div className="single-tab-content">
                      <ul>
                        <li>
                          <div
                            style={{
                              display: "inline-flex",
                              flexDirection: "column",
                            }}
                          >
                            <span style={{ fontSize: 18 }}>Java</span>
                            <Line
                              percent="80"
                              strokeWidth="4"
                              strokeColor="#f10101"
                              trailColor="#636060"
                              style={{
                                width: "13rem",
                              }}
                            />
                          </div>
                        </li>

                        <li>
                          <div
                            style={{
                              display: "inline-flex",
                              flexDirection: "column",
                            }}
                          >
                            <span style={{ fontSize: 18 }}>React.js</span>

                            <Line
                              percent="70"
                              strokeWidth="4"
                              strokeColor="#f10101"
                              trailColor="#636060"
                              style={{
                                width: "13rem",
                              }}
                            />
                          </div>
                        </li>
                        <li>
                          <div
                            style={{
                              display: "inline-flex",
                              flexDirection: "column",
                            }}
                          >
                            <span style={{ fontSize: 18 }}>Firebase</span>

                            <Line
                              percent="60"
                              strokeWidth="4"
                              strokeColor="#f10101"
                              trailColor="#636060"
                              style={{
                                width: "13rem",
                              }}
                            />
                          </div>
                        </li>
                        <li>
                          <div
                            style={{
                              display: "inline-flex",
                              flexDirection: "column",
                            }}
                          >
                            <span style={{ fontSize: 18 }}>TypeScript</span>

                            <Line
                              percent="50"
                              strokeWidth="4"
                              strokeColor="#f10101"
                              trailColor="#636060"
                              style={{
                                width: "13rem",
                              }}
                            />
                          </div>
                        </li>
                        <li>
                          <div
                            style={{
                              display: "inline-flex",
                              flexDirection: "column",
                            }}
                          >
                            <span style={{ fontSize: 18 }}>JavaScript</span>

                            <Line
                              percent="50"
                              strokeWidth="4"
                              strokeColor="#f10101"
                              trailColor="#636060"
                              style={{
                                width: "13rem",
                              }}
                            />
                          </div>
                        </li>
                      </ul>
                    </div>
                  </TabPanel>

                  <TabPanel>
                    <div className="single-tab-content">
                      <ul>
                        <li>
                          <Link
                            style={{ cursor: "pointer" }}
                            activeClass="active"
                            to="portfolio"
                            spy={true}
                            smooth={true}
                            offset={-70}
                            duration={1000}
                          >
                            Intern Backend Developer<span> - Redmind</span>
                          </Link>
                          2020 - Current
                        </li>
                        <li>
                          <Link
                            style={{ cursor: "pointer" }}
                            activeClass="active"
                            to="portfolio"
                            spy={true}
                            smooth={true}
                            offset={-70}
                            duration={1000}
                          >
                            Intern Frontend Developer<span> - Redmind</span>
                          </Link>
                          2020 - Current
                        </li>
                      </ul>
                    </div>
                  </TabPanel>

                  <TabPanel>
                    <div className="single-tab-content">
                      <ul>
                        <li>
                          <a href="/service">
                            Vocational Degree
                            <span> - Nackademin AB, Stockholm</span>
                          </a>{" "}
                          Current
                        </li>
                        <li>
                          <a href="/service">
                            Upper Secondary Diploma
                            <span> - Huddingegymnasiet, Stockholm</span>
                          </a>{" "}
                          2012
                        </li>
                      </ul>
                    </div>
                  </TabPanel>
                </Tabs>
              </div>
            </div>
          </div>
        </div>
        {/* End Tabs Area */}
      </div>
    );
  }
}

export default TabsTwo;
